<script>
import io from 'socket.io-client';
// let socket = io('http://10.75.23.43:8888');
let socket = null;


import light from '../components/breathLight.vue'
import add1 from '../components/add1.vue';
import unicom from '../components/unicom.vue';



export default {
  name: 'HomePage',
  data() {
    return {
      pageName: 'home',
      demo1Num: 0,
      demo2Num: 0,
      demo3Num: 0,
      active: null,
      showAdd: false,
      showUnicom: false,
      innerName: '1',
      innerNum: 0,
      bottom3Num: 1,
      bottom3window: false,
      bottom3Light: false,
      inner2: false,
      inner3: false



    };
  },
  components: {
    light,
    add1,
    unicom,
  },
  async mounted() {
    window.aaa = this;

    // this.$nextTick(() => {
    //   this.$refs[`homeVideo`].play();
    // })

    await fetch('data.json').then(res => {

      return res.json();
    }).then(data => {
      socket = io(`http://${data.ip}:8888`);
    }).catch(err => {
      console.log(err);
    })

    // 监听连接成功事件
    socket.on('connect', () => {
      console.log('Socket 链接成功!');
    });

    // 监听连接成功事件
    socket.on('connect_error', error => {
      console.log('Socket 连接失败:' + error);
    });



    socket.on('pageControl', (data) => {

      if (data.order == 'add1') {
        return
      }

      if (data.order == 'reset') {
        return
      }

      if (data.order == 'bottom3') {

        if (data.msg == 'closeLight') {
          this.bottom3window = false;
          return
        }

        if (data.msg == 'light') {
          this.bottom3window = true;
          return
        }

        this.bottom3Num = data.msg;
        return
      }

      if (data.order == 'bottom2') {
        this.showAdd = 'add2';
        // console.log(data.msg);
        this.innerNum = data.msg;
        return
      }

      if (data == 'closeAdd') {
        this.showAdd = false
        return
      }

      if (data.order == 'showAdd') {
        this.showAdd = data.msg;
        return
      }

      if (data == 'changeLang') {
        this.changeLang();
        return
      }

      this.pageName = data

      if (this.pageName == 'home') this.active = null
      if (this.pageName != 'home') this.active = data.substr(-1);



    })

  },
  computed: {
    PageLang() {
      return this.$route.query.lang
    },
  },
  watch: {
     inner3(newV, oldV) {
      if (newV) {
        this.$refs.bottom3VideoInner3.play();
      } else {
        this.$refs.bottom3VideoInner3.pause();
        this.$refs.bottom3VideoInner3.currentTime = 0;
      }
    },
    inner2(newV, oldV) {
      if (newV) {
        this.$refs.bottom3VideoInner2.play();
      } else {
        this.$refs.bottom3VideoInner2.pause();
        this.$refs.bottom3VideoInner2.currentTime = 0;
      }
    },
    bottom3Num(newV, oldV) {
      // 内页视频组初始化
      let newVideo = this.$refs[`bottom3Video${newV}`];
      let oldVideo = this.$refs[`bottom3Video${oldV}`];
      if (oldVideo) {
        oldVideo.pause();
        oldVideo.currentTime = 0;
      }
      if (this.showAdd == 'add3') {
        if (newVideo) newVideo.play();
      }
    },
    showAdd(newV, oldV) {

      if (newV == 'oldV') {
        this.innerNum = 0
      }

      if (newV == 'add3') {
        this.$refs.bottom3Video1.play();
      }



      if (oldV == 'add2') {
        this.innerNum = 0;
      }


      if (oldV == 'add3') {
        this.bottom3Num = 1;
        this.$refs.bottom3Video1.pause();
        this.$refs.bottom3Video1.currentTime = 0;

      }


    },
    pageName(newV, oldV) {

      // 内页视频组初始化

      socket.emit('controlVideo', newV);


      if (newV == 'home') this.active = null


      if (newV == 'demo2') {
        this.$refs.inner1.play()
        this.$refs.inner2.play()
      }

      if (oldV == 'demo2') {
        this.$refs.inner1.pause()
        this.$refs.inner2.pause()
      }




    },
    PageLang: {
      // immediate:true,
      handler(newV) {

        this.$i18n.locale = newV;


      }
    },

  },
  methods: {
    handleBottom3Video() {

      console.log('end');

    },
    controlGround(msg) {
      let obj = {
        order: 'bottom3',
        msg
      }
      socket.emit('controlVideo', obj);
      this.bottom3Num = msg

    },
    closeAdd() {
      this.showAdd = false
      socket.emit('controlVideo', 'closeAdd');

    },
    toShowAdd(msg) {


      this.showAdd = msg;
      let obj = {
        order: 'showAdd',
        msg
      }
      socket.emit('controlVideo', obj);

    },
    Lang() {
      this.changeLang();
      socket.emit('controlVideo', 'changeLang');

    },
    //中英文切换
    changeLang() {

      if (this.PageLang == 'en-US') {
        this.$router.push({
          query: {
            lang: 'cn-ZH'
          }
        });
      } else {
        this.$router.push({
          query: {
            lang: 'en-US'
          }
        });
      }
    },


  },
};
</script>

