      <video muted ref="12_AI_PV" src="../assets/video/add1/12_AI_PV.mp4" class="videoBg" :class="{'visible':showVideo=='12_AI_PV'}"></video>
      <video muted ref="20" src="../assets/video/add1/20.mp4" class="videoBg" :class="{'visible':showVideo=='20'}"></video>
      <video muted ref="21" src="../assets/video/add1/21.mp4" class="videoBg" :class="{'visible':showVideo=='21'}"></video>
