        <img src="../assets/images/add1/icon3.png" style="margin-top:70px;margin-left:23px;" alt="">
        <img src="../assets/images/add1/icon4.png" style="margin-top:90px" alt="">
        <img src="../assets/images/add1/icon5.png" style="margin-top:130px" alt="">
        <img src="../assets/images/add1/icon6.png" style="margin-top:70px" alt="">
        <img src="../assets/images/add1/icon7.png" style="margin-top:75px" alt="">
        <img src="../assets/images/add1/icon8.png" style="margin-top:155px" alt="">
      </div>

      <!-- 按钮 -->
      <div class="btnList" :class="{'btnSubEn':PageLang=='en-US'}">
        <div @click="changeData('PV',true)" class="btnSub" :class="{'btnSub1':all.PV==true}">{{$t('add1.btnFont1')}}</div>
        <div @click="changeData('system1',0)" class="btnSub" :class="{'btnSub1':all.system1==0}">{{$t('add1.btnFont2')}}</div>
        <div @click="changeData('system1',1)" class="btnSub" :class="{'btnSub1':all.system1==1}">{{$t('add1.btnFont3')}}</div>
        <div @click="changeData('system1',2)" class="btnSub" :class="{'btnSub1':all.system1==2}">{{$t('add1.btnFont4')}}</div>
      </div>

      <!-- 按钮 -->
      <div class="btnList1" :class="{'btnSubEn':PageLang=='en-US'}">
        <div @click="changeData('system2',0)" class="btnSub" :class="{'btnSub1':all.system2==0,'lineHeightEn':PageLang=='en-US'}">{{$t('add1.btnFont5')}}</div>
        <div @click="changeData('system2',1)" class="btnSub" :class="{'btnSub1':all.system2==1,'lineHeightEn':PageLang=='en-US'}">{{$t('add1.btnFont6')}}</div>
        <div @click="changeData('system2',2)" class="btnSub" :class="{'btnSub1':all.system2==2}">{{$t('add1.btnFont7')}}</div>
        <div @click="changeData('AI',true)" class="btnSub" :class="{'btnSub1':all.AI==true,'lineHeightEn':PageLang=='en-US'}">{{$t('add1.btnFont8')}}</div>
      </div>

    </div>

    <!-- 左边菜单 -->
    <div class="leftMenu" :class="{'showMenu':showMenu}">
      <!-- 菜单背景 -->
      <img :src="$t('add1.leftMenu')" class="bg" alt="">

      <!-- 节省数值 -->
      <div class="saveFont save1" :class="{'saveFontEn':PageLang=='en-US'}">
        {{$t('add1.saveFont')}}
        <div v-if="!flag" class="saveNum">0</div>
        <animeNum v-if="flag" :from="leftData1.saveNumOld" :to="leftData1.saveNum" :time="1000" class="saveNum"></animeNum>
      </div>

      <!-- 节省数值 -->
      <div class="saveFont save2" :class="{'saveFontEn':PageLang=='en-US'}">
        {{$t('add1.saveFont')}}
        <div v-if="!flag" class="saveNum">0</div>
        <animeNum v-if="flag" :from="leftData2.saveNumOld" :to="leftData2.saveNum" :time="1000" class="saveNum"></animeNum>
      </div>

      <!-- 节省数值 -->
      <div class="saveFont save3" :class="{'saveFontEn':PageLang=='en-US'}">
        {{$t('add1.saveFont')}}
        <div v-if="!flag" class="saveNum">0</div>
        <animeNum v-if="flag" :from="leftData3.saveNumOld" :to="leftData3.saveNum" :time="1000" class="saveNum"></animeNum>

      </div>

      <!-- 推荐方案 绿色柱子 -->
      <div class="greenBox greenBox1" :class="{'greenBox1En':PageLang=='en-US'}">
        <div v-if="!flag" class="greenNum">0</div>
        <animeNum v-if="flag" :from="leftData1.greenNumOld" :to="leftData1.greenNum" :time="1000" class="greenNum"></animeNum>
        <img src="../assets/images/add1/green.png" class="green" :style="`height:${leftData1.height}px;`" alt="">
      </div>

      <div class="greenBox greenBox2" :class="{'greenBox2En':PageLang=='en-US'}">
        <div v-if="!flag" class="greenNum">0</div>
        <animeNum v-if="flag" :from="leftData2.greenNumOld" :to="leftData2.greenNum" :time="1000" class="greenNum"></animeNum>
        <img src="../assets/images/add1/green.png" class="green" :style="`height:${leftData2.height}px;`" alt="">
      </div>

      <div class="greenBox greenBox3" :class="{'greenBox3En':PageLang=='en-US'}">
        <div v-if="!flag" class="greenNum">0</div>
        <animeNum v-if="flag" :from="leftData3.greenNumOld" :to="leftData3.greenNum" :time="1000" :initState="true" class="greenNum"></animeNum>
        <img src="../assets/images/add1/green.png" class="green" :style="`height:${leftData3.height}px;`" alt="">
      </div>

    </div>

    <div @click="this.showMenu=!this.showMenu" class="showMenuDiv"></div>

    <img @click="toReset()" src="../assets/images/BTN/loop.png" class="Btn2" alt="">

  </div>
</template>

<script>
import io from 'socket.io-client';
import animeNum from '../components/animate-number.vue'
import { toRaw } from 'vue';
import _ from 'lodash';
// let socket = io('http://10.75.23.43:8888');
let socket = null;



export default {
  name: '',
  data() {
    return {
      all: {
        system1: 0,
        system2: 0,
        PV: false,
        AI: false,
      },
      btnAll: {
        system1: 0,
        system2: 0,
        PV: false,
        AI: false,
      },
      leftData1: {
        saveNum: 0,
        saveNumOld: 0,
        height: 0,
        greenNum: 0,
        greenNumOld: 0
      },
      leftData2: {
        saveNum: 0,
        saveNumOld: 0,
        height: 0,
        greenNum: 0,
        greenNumOld: 0
      },
      leftData3: {
        saveNum: 0,
        saveNumOld: 0,
        height: 0,
        greenNum: 0,
        greenNumOld: 0
      },
      showVideo: null,
      timer: null,
      flag: false,
      showTip: false,
      showMenu: false

    };
  },
  components: {
    animeNum
  },
  async mounted() {

    await fetch('data.json').then(res => {

      return res.json();
    }).then(data => {
      socket = io(`http://${data.ip}:8888`);
    }).catch(err => {
      console.log(err);
    })


    socket.on('pageControl', (data) => {

      if (data.order == 'add1') {

        this.flag = true;

        // this.showMenu = true;

        this.all[`${data.msg}`] = data.result;

        return
      }

      if (data.order == 'reset') {

        this.reset();

        return
      }


    })
  },
  computed: {
    PageLang() {
      return this.$route.query.lang
    },
  },
  watch: {
    'leftData3.saveNum'(newV, oldV) {
      this.leftData3.saveNumOld = oldV
    },
    'leftData2.saveNum'(newV, oldV) {
      this.leftData2.saveNumOld = oldV
    },
    'leftData1.saveNum'(newV, oldV) {
      this.leftData1.saveNumOld = oldV
    },
    'leftData3.greenNum'(newV, oldV) {
      this.leftData3.greenNumOld = oldV
    },
    'leftData2.greenNum'(newV, oldV) {
      this.leftData2.greenNumOld = oldV
    },
    'leftData1.greenNum'(newV, oldV) {
      this.leftData1.greenNumOld = oldV
    },
    all: {
      deep: true,
      handler(newV, oldV) {

        // console.log(newV);
        // console.log(oldV);

        // for (let key in newV) {
        //   if (newV[key] !== oldV[key]) {
        //     console.log(`属性${key} 改变:`, newV[key]);
        //   }
        // }

        this.mappingForShowVideo();


      }
    },
    showVideo(newV, oldV) {
      console.log('视频名字:' + newV);
      // 内页视频组初始化
      let newVideo = this.$refs[`${newV}`];
      let oldVideo = this.$refs[`${oldV}`];
      if (oldVideo) {
        oldVideo.pause();
        oldVideo.currentTime = 0;
      }
      if (newVideo) {
        newVideo.play();
      } else {
        console.log('不存在该组合');
        if (this.showVideo != '00') {

          this.showTip = true;
          setTimeout(() => {
            this.showTip = false
          }, 2000)

        }
      }

    },
  },
  methods: {
    toReset() {

      let obj = {
        order: 'reset'
      }
      socket.emit('controlVideo', obj);
      this.reset();
    },
    reset() {
      this.all.system1 = 0;
      this.all.system2 = 0;
      this.all.PV = false;
      this.all.AI = false;

      this.flag = false


      this.leftData1 = {
        saveNum: 0,
        saveNumOld: 0,
        height: 0,
        greenNum: 0,
        greenNumOld: 0
      }

      this.leftData2 = {
        saveNum: 0,
        saveNumOld: 0,
        height: 0,
        greenNum: 0,
        greenNumOld: 0
      }

      this.leftData3 = {
        saveNum: 0,
        saveNumOld: 0,
        height: 0,
        greenNum: 0,
        greenNumOld: 0
      }

      this.showMenu = false


    },
    //防抖 （相当于lol的回城 ）
    debounce(fn, time) {

      if (this.timer !== null) clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        fn();
      }, time);

    },
    mappingForData() {

      if (this.showVideo == '10') {
        this.leftData1.saveNum = '16.8';
        this.leftData1.greenNum = '243.1';
        this.leftData1.height = '170';

        this.leftData2.saveNum = '2.5';
        this.leftData2.greenNum = '35.9';
        this.leftData2.height = '85';

        this.leftData3.saveNum = '0';
        this.leftData3.greenNum = '11.5';
        this.leftData3.height = '85';
      }

      if (this.showVideo == '11') {
        this.leftData1.saveNum = '88.4';
        this.leftData1.greenNum = '171.5';
        this.leftData1.height = '130';

        this.leftData2.saveNum = '13.1';
        this.leftData2.greenNum = '25.3';
        this.leftData2.height = '60';

        this.leftData3.saveNum = '0';
        this.leftData3.greenNum = '11.5';
        this.leftData3.height = '85';
      }

      if (this.showVideo == '11_AI') {
        this.leftData1.saveNum = '91.6';
        this.leftData1.greenNum = '168.3';
        this.leftData1.height = '120';

        this.leftData2.saveNum = '13.5';
        this.leftData2.greenNum = '24.9';
        this.leftData2.height = '38';

        this.leftData3.saveNum = '0';
        this.leftData3.greenNum = '11.5';
        this.leftData3.height = '85';
      }

      if (this.showVideo == '11_AI_PV') {
        this.leftData1.saveNum = '159.2';
        this.leftData1.greenNum = '100.7';
        this.leftData1.height = '75';

        this.leftData2.saveNum = '23.5';
        this.leftData2.greenNum = '14.9';
        this.leftData2.height = '42';

        this.leftData3.saveNum = '0';
        this.leftData3.greenNum = '11.5';
        this.leftData3.height = '85';
      }

      if (this.showVideo == '12') {
        this.leftData1.saveNum = '94.1';
        this.leftData1.greenNum = '165.8';
        this.leftData1.height = '120';

        this.leftData2.saveNum = '13.9';
        this.leftData2.greenNum = '24.5';
        this.leftData2.height = '50';

        this.leftData3.saveNum = '0';
        this.leftData3.greenNum = '11.5';
        this.leftData3.height = '85';
      }

      if (this.showVideo == '12_AI') {
        this.leftData1.saveNum = '97.3';
        this.leftData1.greenNum = '162.6';
        this.leftData1.height = '117';

        this.leftData2.saveNum = '14.4';
        this.leftData2.greenNum = '24.0';
        this.leftData2.height = '38';

        this.leftData3.saveNum = '0';
        this.leftData3.greenNum = '11.5';
        this.leftData3.height = '85';
      }

      if (this.showVideo == '12_AI_PV') {
        this.leftData1.saveNum = '162.6';
        this.leftData1.greenNum = '97.3';
        this.leftData1.height = '63';

        this.leftData2.saveNum = '24';
        this.leftData2.greenNum = '14.4';
        this.leftData2.height = '30';

        this.leftData3.saveNum = '0';
        this.leftData3.greenNum = '11.5';
        this.leftData3.height = '85';
      }

      if (this.showVideo == '20') {
        this.leftData1.saveNum = '22.2';
        this.leftData1.greenNum = '237.7';
        this.leftData1.height = '155';

        this.leftData2.saveNum = '3.3';
        this.leftData2.greenNum = '35.1';
        this.leftData2.height = '80';

        this.leftData3.saveNum = '4.5';
        this.leftData3.greenNum = '7';
        this.leftData3.height = '55';
      }

      if (this.showVideo == '21') {
        this.leftData1.saveNum = '93.9';
        this.leftData1.greenNum = '166.0';
        this.leftData1.height = '125';

        this.leftData2.saveNum = '13.9';
        this.leftData2.greenNum = '24.5';
        this.leftData2.height = '50';

        this.leftData3.saveNum = '4.5';
        this.leftData3.greenNum = '7';
        this.leftData3.height = '55';
      }

      if (this.showVideo == '21_AI') {
        this.leftData1.saveNum = '97.0';
        this.leftData1.greenNum = '162.9';
        this.leftData1.height = '120';

        this.leftData2.saveNum = '14.3';
        this.leftData2.greenNum = '24.1';
        this.leftData2.height = '47';

        this.leftData3.saveNum = '4.5';
        this.leftData3.greenNum = '7';
        this.leftData3.height = '55';
      }

      if (this.showVideo == '21_AI_PV') {
        this.leftData1.saveNum = '162.4';
        this.leftData1.greenNum = '97.5';
        this.leftData1.height = '65';

        this.leftData2.saveNum = '24.0';
        this.leftData2.greenNum = '14.4';
        this.leftData2.height = '30';

        this.leftData3.saveNum = '4.5';
        this.leftData3.greenNum = '7';
        this.leftData3.height = '55';
      }

      if (this.showVideo == '22') {
        this.leftData1.saveNum = '99.6';
        this.leftData1.greenNum = '160.3';
        this.leftData1.height = '115';

        this.leftData2.saveNum = '14.7';
        this.leftData2.greenNum = '23.7';
        this.leftData2.height = '40';

        this.leftData3.saveNum = '4.5';
        this.leftData3.greenNum = '7';
        this.leftData3.height = '55';
      }

      if (this.showVideo == '22_AI') {
        this.leftData1.saveNum = '102.7';
        this.leftData1.greenNum = '157.2';
        this.leftData1.height = '105';

        this.leftData2.saveNum = '15.2';
        this.leftData2.greenNum = '23.2';
        this.leftData2.height = '38';

        this.leftData3.saveNum = '4.5';
        this.leftData3.greenNum = '7';
        this.leftData3.height = '55';
      }

      if (this.showVideo == '22_AI_PV') {
        this.leftData1.saveNum = '165.8';
        this.leftData1.greenNum = '94.1';
        this.leftData1.height = '60';

        this.leftData2.saveNum = '24.5';
        this.leftData2.greenNum = '13.9';
        this.leftData2.height = '25';

        this.leftData3.saveNum = '4.5';
        this.leftData3.greenNum = '7';
        this.leftData3.height = '55';
      }






    },
    mappingForShowVideo() {

      let system1 = this.all.system1;
      let system2 = this.all.system2;
      let AI = this.all.AI;
      let PV = this.all.PV;

      let tempName = '';

      tempName = `${system1}${system2}`
      if (AI) tempName += '_AI';
      if (PV) tempName += '_PV';

      this.showVideo = tempName;

      this.mappingForData();


    },
    changeData(msg, result) {
      this.flag = true;



      this.$nextTick(() => {
        this.all[`${msg}`] = result;

        let obj = {
          order: 'add1',
          msg,
          result
        }

        socket.emit('controlVideo', obj);


      })


      //及时反馈到按钮过渡变量先
      // this.btnAll[`${msg}`] = result;

      //  //防抖写法
      // if (this.timer !== null) clearTimeout(this.timer);
      // this.timer = setTimeout(() => {
      //   this.all[`${msg}`] = result;
      //   //深拷贝 重新赋值给按钮过渡效果变量  避免两个变量之间相互影响
      //   this.btnAll=_.cloneDeep(this.all);

      // }, 300);


    }



  },
};
</script>

<style scoped>
