  width: 100%;
  height: 100%;
}

.middleFont {
  position: absolute;
  left: 1820px;
  top: 670px;
  /* -webkit-backdrop-filter: blur(880px); */
  background-color: #11141a;
  width: 200px;
  height: 70px;
  z-index: 100;
}

.middleFont::after {
  content: "IT Load Rate";
