  transition-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.btnList {
  position: absolute;
  right: 86px;
  top: 455px;
  width: 510px;
  height: 570px;
  /* opacity: 0.6; */
  /* background-color: red; */
}

.btnList1 {
  position: absolute;
  right: 90px;
  top: 1050px;
  width: 510px;
  height: 650px;
  /* opacity: 0.6; */
  /* background-color: red; */
}

.btnSub {
  width: 502px;
  height: 106px;
  background: url("../assets/images/add1/select.png") no-repeat;
  margin-top: 35px;
  font-size: 50px;
  color: #fefefe;
  text-align: left;
  line-height: 106px;
  padding-left: 200px;
  cursor: pointer;
