<template>
  <div :style="`color:rgb(${R},${G},${B})`">{{tempNum}}</div>
</template>

<script>
export default {
  name: 'animate-number',
  data() {
    return {
      tempNum: '',
      fromNum: this.from,
      resultNum: this.to,
      R: 0,
      tempR: 0,
      G: 0,
      tempG: 0,
      B: 0,
      tempB: 0,
      D_R: 0,
      D_G: 0,
      D_B: 0,
      originRGB: [0, 0, 0],
      resultRGB: [0, 0, 0],
      colorFlag: true,
      commaFlag: false,
      pointFlag: false,
      objArr: null,
      pointArr: null,
      intervalValue: null,


    };
  },
  computed: {
  },
  //from 初始值 to 最终值 time 动画持续时间 delay 动画延迟播放时间  originColor 原颜色 resultColor 最终颜色
  props: ['from', 'to', 'time', 'delay', 'originColor', 'resultColor'],
  mounted() {

   this.init();

  },
  watch: {
    to(newV, oldV) {

      this.resultNum = newV;
      this.fromNum = oldV;
      this.init();
    }


  },
  methods: {
    //主逻辑函数
    animeNum(fromNum, resultNum, time, step, endNum, bothMinus) {

      /* 
      整体逻辑 
      利用requestAnimationFrame每16.7ms刷新一次的特性，在每次循环让step累加，通过判断step的值，来判断是否要退出循环
      在每次循环的时候，通过字符串拼接的方式，动态刷新数字实现数字跳动,如果动画时间结束，则把真实结果值赋上 
      */

      let temp = null;

      requestAnimationFrame(() => {

        if (step < time) {

          step += 17;
          let tempD = 0;
          let rate = 0;


          // 判断fromNum长度与resultNum长度是否相等
          let flag = fromNum.length == resultNum.length ? true : false;


          // 判断是否含负数 且长度是否相等
          if ((fromNum.includes('-') && flag) || (resultNum.includes('-') && flag)) {
            tempD = 1;
          }

          // 判断两个都含有负数
          if (fromNum.includes('-') && resultNum.includes('-')) {
            fromNum = fromNum.substring(1, fromNum.length);
            resultNum = resultNum.substring(1, resultNum.length);
            endNum = endNum.substring(0, endNum.length - 1);
            tempD = 0;
            bothMinus = true;
          }


          // 首数字变化逻辑
          let D_Value = 1 + tempD; //定义fromNum与resultNum长度差值

          if (flag) {
            D_Value = 2 + tempD;
          }


          // 数字增长比率逻辑
          rate = time / (Number(resultNum.substring(0, D_Value)) - Number(fromNum.substring(0, D_Value)));

          // 首数字变化逻辑
          let firstNum = Number(fromNum.substring(0, D_Value));


          //判断fromNum长度与resultNum长度是否相等
          if (fromNum.length > resultNum.length) {

            D_Value += (fromNum.length - resultNum.length);

            firstNum = Number(fromNum.substring(0, D_Value));

            rate = time / (Number(resultNum.substring(0, 1)) - Number(fromNum.substring(0, D_Value)));

          }

          if (resultNum.length > fromNum.length) {

            D_Value += (resultNum.length - fromNum.length);

            firstNum = Number(fromNum.substring(0, 1));

            rate = time / (Number(resultNum.substring(0, D_Value)) - Number(fromNum.substring(0, 1)));

          }



          // 首数字变化逻辑
          let dynamicNum = Math.floor(step / rate);

          //通过colorFlag判断以下代码有无必要执行
          if (this.colorFlag) {
            //颜色变化相关逻辑 
            let r_num = time / this.D_R;
            let g_num = time / this.D_G;
            let b_num = time / this.D_B;

            //颜色变化相关逻辑 
            let r = Math.floor(step / r_num);
            let g = Math.floor(step / g_num);
            let b = Math.floor(step / b_num);

            //颜色变化相关逻辑 
            this.R = this.tempR + r;
            this.G = this.tempG + g;
            this.B = this.tempB + b;

          }




          if (step >= rate) {
            //首数字变化相关逻辑
            firstNum += dynamicNum;

          }

          // 尾数字变化逻辑
          let numArr = new Array();

          for (let i of endNum) {
            numArr.push(Number(i));
          }
          endNum = "";
          for (let i of numArr) {

            if (i < 9) {
              i += 1;
            } else {
              i = 0;
            }
            endNum += i;
          }


          //是否含有负数且fromNum和resultNum是否相等
          if (tempD == 1) {
            temp = `${firstNum}${endNum.substring(0, endNum.length - 1)}`;
            temp = this.commaFlag ? this.handleCommaResult(temp) : temp;
            temp = this.pointFlag ? this.handlePointResult(temp) : temp;
            this.tempNum = temp;
          } else {

            //是否含有负数且fromNum和resultNum是否相等
            if (bothMinus) {
              temp = `-${firstNum}${endNum}`;
              temp = this.commaFlag ? this.handleCommaResult(temp) : temp;
              temp = this.pointFlag ? this.handlePointResult(temp) : temp;

              this.tempNum = temp;
            } else {

              temp = `${firstNum}${endNum}`;
              temp = this.commaFlag ? this.handleCommaResult(temp) : temp;
              temp = this.pointFlag ? this.handlePointResult(temp) : temp;
              // if (temp.includes('.')) temp = temp.substring(0, temp.indexOf(".") + 2);
              // console.log(temp);

              this.tempNum = temp;

            }


          }

          //赋值
          this.animeNum(fromNum, resultNum, time, step, endNum, bothMinus);

        } else {

          // console.log(this.resultNum);
          // console.log(this.tempNum);
          // this.$nextTick(() => {
          //   this.tempNum = this.resultNum
          // })

          //最终赋值
          if (bothMinus) {
            temp = `-${resultNum}`;
            temp = this.commaFlag ? this.handleCommaResult(temp) : temp;
            temp = this.pointFlag ? this.handlePointResult(temp) : temp;

            this.tempNum = temp;
          } else {
            temp = resultNum;
            temp = this.commaFlag ? this.handleCommaResult(temp) : temp;
            temp = this.pointFlag ? this.handlePointResult(temp) : temp;

            this.tempNum = temp;
          }

           this.tempNum=this.to;

          //颜色最终赋值
          this.R = this.resultRGB[0];
          this.G = this.resultRGB[1];
          this.B = this.resultRGB[2];

        }

      })


    },
    //重置数字跳动动画
    resetAnime() {

      //  let i= window.crypto.getRandomValues(new Uint32Array(1))[0] / (Math.pow(2, 32) - 1);
      //  console.log(Math.round(i*10));

      this.fromNum += "";
      this.resultNum += "";
      let endNum = null;

      if (this.fromNum.length > this.resultNum.length) {

        endNum = this.generateEndNum(this.resultNum);

      } else {

        endNum = this.generateEndNum(this.fromNum);

      }

      if (this.delay == undefined || this.delay == 0) {

        this.animeNum(this.fromNum, this.resultNum, this.time, 17, endNum);

      } else {
        setTimeout(() => {
          this.animeNum(this.fromNum, this.resultNum, this.time, 17, endNum);
        }, this.delay)

      }


    },
    newGenerateEndNum(resultNum) {
      let tempStr = "";
      let temp = null;

      for (let i = 0; i < resultNum.length - 2; i++) {
        temp = Math.floor(Math.random() * 10);
        tempStr += temp;
      }


      return tempStr;
    },
    //生成随机尾数
    generateEndNum(resultNum) {
      let tempStr = "";
      let temp = null;

      for (let i = 0; i < resultNum.length - 1; i++) {
        temp = Math.floor(Math.random() * 10);
        tempStr += temp;
      }

      return tempStr;
    },
    //处理小数点
    handlePointPre(num) {
      let numArr = num.split('');
      let objArr = new Array();

      let newArr = numArr.filter((item, index, arr) => {

        if (item == ".") {
          objArr.push({
            content: item,
            arrIndex: arr.length - index - 1
          })
        }

        return item != "."

      })

      this.pointArr = objArr;

      return newArr.join('')



    },
    //处理小数点
    handlePointResult(num) {
      let newArr = num.split('');
      newArr.splice(newArr.length - this.pointArr[0].arrIndex, 0, this.pointArr[0].content);

      return newArr.join('');


    },
    //处理逗号功能函数  
    handleCommaPre(num) {

      let numArr = num.split('');
      let objArr = new Array();


      let newArr = numArr.filter((item, index, arr) => {

        if (item == ",") {
          objArr.push({
            content: item,
            arrIndex: index
          })
        }

        return item != ","

      })

      this.objArr = objArr;

      console.log(this.objArr);


      return newArr.join('')


    },
    //处理逗号功能函数 
    handleCommaResult(num) {

      let length = num.length
      let newArr = num.split('');

      if (length / this.intervalValue > 1) {

        let temp = Math.floor(length / this.intervalValue);
        if (length % this.intervalValue == 0) temp--;


        for (var i = 0; i < temp; i++) {

          newArr.splice(this.objArr[i].arrIndex, 0, this.objArr[i].content);

        }


      }

      return newArr.join('');



    },
    //初始化
    init() {
      //将传来的值全部统一转成字符串
      this.fromNum += "";
      this.resultNum += "";

      // 判断是否存在逗号
      if (this.fromNum.includes(',') && this.resultNum.includes(',')) {

        //求出逗号间隔值
        this.intervalValue = (this.fromNum.length - 1) - this.fromNum.lastIndexOf(",");

        //调用处理函数去除逗号
        if (this.fromNum.length > this.resultNum.length) {
          this.resultNum = this.handleCommaPre(this.resultNum);
          this.fromNum = this.handleCommaPre(this.fromNum);
        } else {
          this.fromNum = this.handleCommaPre(this.fromNum);
          this.resultNum = this.handleCommaPre(this.resultNum);
        }

        this.commaFlag = true;

      }

      //判断是否存在小数点
      if (this.fromNum.includes('.') && this.resultNum.includes('.')) {

        //调用处理函数去除小数点
        if (this.fromNum.length > this.resultNum.length) {
          this.resultNum = this.handlePointPre(this.resultNum);
          this.fromNum = this.handlePointPre(this.fromNum);
        } else {
          this.fromNum = this.handlePointPre(this.fromNum);
          this.resultNum = this.handlePointPre(this.resultNum);
        }

        this.pointFlag = true;

      }


      //赋初始值
      this.tempNum = this.from;

      //判断传来的颜色值是否为空值
      if (this.originColor !== undefined) {
        //将传来的值全部统一转成数字并赋值
        this.originRGB[0] = Number(this.originColor[0]);
        this.originRGB[1] = Number(this.originColor[1]);
        this.originRGB[2] = Number(this.originColor[2]);
      }
      //判断传来的颜色值是否为空值
      if (this.resultColor !== undefined) {
        //将传来的值全部统一转成数字并赋值
        this.resultRGB[0] = Number(this.resultColor[0]);
        this.resultRGB[1] = Number(this.resultColor[1]);
        this.resultRGB[2] = Number(this.resultColor[2]);
      }

      //判断颜色值都传来空值的话 颜色相关逻辑代码则不执行
      if (this.resultColor === undefined && this.originColor === undefined) {
        this.colorFlag = false;
      }


      //赋初始颜色
      this.R = this.originRGB[0];
      this.G = this.originRGB[1];
      this.B = this.originRGB[2];
      this.tempR = this.originRGB[0];
      this.tempG = this.originRGB[1];
      this.tempB = this.originRGB[2];


      //算出rgb差值
      this.D_R = this.resultRGB[0] - this.R;
      this.D_G = this.resultRGB[1] - this.G;
      this.D_B = this.resultRGB[2] - this.B;



      //定义尾数
      let endNum = null;


      //判断初始值和最终值的长度是否相等 并根据结果生成对应的尾数
      if (this.fromNum.length > this.resultNum.length) {
        endNum = this.generateEndNum(this.resultNum);
      } else if (this.resultNum.length > this.fromNum.length) {
        endNum = this.generateEndNum(this.fromNum);
      }

      // 初始值和最终值的长度相等
      if (this.fromNum.length == this.resultNum.length) {
        endNum = this.newGenerateEndNum(this.fromNum);
      }



      //判断动画延迟时间并根据结果执行相应逻辑
      if (this.delay == undefined || this.delay == 0) {

        this.animeNum(this.fromNum, this.resultNum, this.time, 17, endNum);

      } else {
        setTimeout(() => {
          this.animeNum(this.fromNum, this.resultNum, this.time, 17, endNum);
        }, this.delay)

      }


    }

  },


};
</script>

